import axios from 'axios';
import AxiosError from 'axios-error';
import urlJoin from 'url-join';
import BulletsAnimation from '../../../../includes/assets/js/modules/BulletsAnimation';

export default class DPSettingsActions {

	constructor(config) {
		if (config.dpDebugEn) console.log('DPSettingsActions');
		this.config = config;
		this.bulletsAnimation = [];
		this.registerActions(['clear_visit_count']);
	}

	registerActions(actionNames) {

		actionNames.forEach((name) => {
			const id = `dpit_action_${name}`;
			this.bulletsAnimation[id] = new BulletsAnimation('#' + id, id + '_animation', ['dpit-admin-btn__bullets'], 8, 100);
			$('#' + id).on('click', () => this.processAction(id, name));
		});
	}

	processAction = (id, actionName) => {
		// adjust settings in db
		const jSrcBtn = $('#' + id);
		const dataToPost = {
			actionName: actionName
		};
		this.bulletsAnimation[id].createAndRun();
		jSrcBtn.addClass('in-process');
		axios.post(
			urlJoin(this.config.siteUrl, '/wp-json/dpintrotours/v1/trigger-action'),
			dataToPost,
			{ headers: { 'X-WP-Nonce': this.config.nonces.wp_rest } }
		)
			.then(() => {
				jSrcBtn.removeClass('in-process');
				const succMsg = jSrcBtn.data('msg-success');
				if (succMsg) jSrcBtn.children('.dpit-admin-btn__text').text(succMsg);
				jSrcBtn.addClass('dpit-admin-btn--success');
				this.bulletsAnimation[id].destroy();
				setTimeout(() => {
					jSrcBtn.children('.dpit-admin-btn__text').text(jSrcBtn.data('orig-title'));
					jSrcBtn.removeClass('dpit-admin-btn--success');
				}, 3000);
			})
			.catch(error => {
				console.log(new AxiosError(error));
				jSrcBtn.removeClass('in-process');
				this.bulletsAnimation[id].destroy();
				const failedMsg = jSrcBtn.data('msg-failed');
				jSrcBtn.addClass('dpit-admin-btn--error');
				if (failedMsg) jSrcBtn.children('.dpit-admin-btn__text').text(failedMsg);
				setTimeout(() => {
					jSrcBtn.children('.dpit-admin-btn__text').text(jSrcBtn.data('orig-title'));
					jSrcBtn.removeClass('dpit-admin-btn--error');
				}, 3000);
			});
	}

}